

a{
  text-decoration: none;
  color: black;
}



/* body{
  overflow: hidden;
} */

.banner {
  display: flex;
  width: 82vw;
  justify-content: space-between;
  background: #ffb5a7;
}

.banner_head {
  font-size: 40px;
  font-weight: 600;
  padding: 50px;
  padding-inline: 50px;
  color: white;
  text-align: right;
  line-height: 40px;
}


.banner div span {
  font-size: 29px;
  text-align: center;
}

.banner img{
  height: 30vh;
}

.nav {
  display: flex;
  align-content: center;
  justify-content: center;
  font-size: large;
  padding-inline: 0px;
  width: 18vw;
  min-width:fit-content;
  height: 100vh;
  background-color: #564ed7;
  color: white;
  position: fixed;
}
.nav ul li a{
  color: white;
}

.nav ul li a:hover{
  color: #b8bfc8;
}
.blank{
  width: 18vw;
  height: 100vh;
}

.main{
  display: flex;
  overflow-x: hidden;
}
.new_temp{
  display: flex;
  justify-content: center;

}
.temp_par{
  display: flex;
  
}
.temp{
  display: flex;
  flex-direction: column;
  margin: 10px;
  align-items: center;
}
.temp img{
  height: 150px;
  width: 150px;
  border-radius: 10px;
}
.temp span{
  width: 100px;
  text-align: center;
}
.butn_def{
  background: none;
	color: inherit;
	border: none;
	padding: 0;
	font: inherit;
	cursor: pointer;
	outline: inherit;
}
#carosuel{
  margin-top: 20px;
}

.left{
  margin-left: 18vw;
}

#first{
  color: #6c757d;
  font-size: 20px;
  font-weight: 600;
}
#sec{
  
  display: inline-block;
  width: 50vw;
  background-color: #6c757d;
  height: 1.2px;
  margin: 10px;
}
#spinner{
  color:  #736cd8;
}
.Jus-Con{
  max-width: min-content;
}
#head{
  align-items: center;
  display: flex;
  margin-block: 12px;
}
.pannel{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-block:50px;
  overflow-x: hidden;
}
.butn{
  background: none;
	color: inherit;
	border: none;
	padding: 0;
	font: inherit;
	cursor: pointer;
	outline: inherit;
  color: #736cd8;
  font-size: x-large;
  margin-inline: 3px;
}
.butn:hover{
  color: #736cd8;
}

.inputbox input{
  border: 1.5px solid #c1c1c1;
  width: 60%;
  height: 6.5vh;
  border-radius: 100px;
  /* padding: 22px; */
  padding-inline: 21px;
}
.inputbox {
  display: flex;
  margin-top: 20px;
  margin-bottom: 20px;
  width: 100%;
  align-items: center;
  justify-content: center;
}
.main_search_box{
  display: flex;
  flex-direction: column;
  align-items: center;
}

.search_butn{
  font-size: 4vh;
  padding: 10px;
  color: #564ed7;

}
.search_aud_butn{
  font-size: 3.75vh;
  padding: 10px;
  color: #564ed7;

}
.item{
  display: flex;
  width: 30vw;
  margin: 20px;
  align-items: center;
  justify-content: space-evenly
}

.item img{
  width: 6.5vw;
  height: 6.5vw;
  border-radius: 8px;
}
.item div{
  width: 23.4vw;
  padding: 15px;
  margin-left: 15px;
}
.list_main{
  max-height: 60vh;
  overflow-y: auto;
}
.list_main::-webkit-scrollbar {
  display: none;
}

.video{
  aspect-ratio: 12/7.5;
  width: 33vw;
  justify-content: center
  
}

.lyric_head{
  justify-content: center;
  height: 33vh;
  overflow-y: auto;
  text-align: center;

}
.lyrics{
  list-style: none;
  line-height: 1.9rem;
  font-weight: 600;
}

.lyrics_heading{
  color:rgb(240 98 59) ;
  text-shadow: -1.5px -1.5px #564ed7;
  /* font-weight: 700; */
}

.play_butn{
  color: rgb(240 98 59);
}
.play_butn:hover{
  color:rgba(239, 133, 103, 0.9) ;
}
.hide{
  display: none;
  position: fixed;
}
.txt:hover + .hide{
  display: block;
  width: 100px;
  border: 1px solid rgb(114, 111, 111);
  background-color: rgb(188, 184, 184);
}
.title_heading{
  max-height: max-content;
  justify-content: center;
} 

@media only screen and (max-width: 420px){
  .main{
    display: block;
  }
  .nav{
    position: fixed;
    display: flex;
    width: 100vw;
    height: 8vh;
  }
  .left{
    margin: 0%;
    margin-top: 8vh;
  }
  .banner{
    width: 100vw;
    flex-direction: column;
  }
  .banner img{
    width: 100vw;
    aspect-ratio: 1.9;
    height: auto;
  }
  .flex-column {
    flex-direction: row!important;
  }
  .banner_head{
    display: none;
  }
  .inputbox input{
    width: 60%;
  }
  .inputbox {
    margin-top: 20px;
    height: 13vh;
    width: 95vw;
    display: flex;
    margin-bottom: 20px;
    align-items: center;
    justify-content: center;
}
.search_butn {
  padding: 1.3vw;
}
}